import _99 from './99.png';
import './App.css';
import { useState } from 'react';

function App() {
	const [onMouse, setOnMouse] = useState(false);
	const handleOnMouse = () => {
		setOnMouse(!onMouse);
	};
	return (
		<div className='App'>
			<header className='App-header'>
				<img src={_99} alt='logo' width='50%' />
				<p>KLAYRock Experiment w/ Rockers!</p>
				<a
					className='App-link'
					href='https://bit.ly/klay-rocker'
					target='_blank'
					rel='noopener noreferrer'
					style={{ color: onMouse ? 'yellowgreen' : 'whitesmoke' }}
					onMouseEnter={() => handleOnMouse()}
					onMouseOut={() => handleOnMouse()}>
					오카방 입장!
				</a>
			</header>
		</div>
	);
}

export default App;
